import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DesignMedia } from '../models/design-media';
import { environment } from '../../environments/environment';

@Injectable()
export class BehanceService {
  private behanceUrl: string;

  constructor(private httpClient: HttpClient) {
    this.behanceUrl = environment.behanceUrl;
  }

  getMedia(): Promise<Array<DesignMedia>> {
    const url = `${this.behanceUrl}/assets/data/behance.json`;
    return this.httpClient
      .get<Array<DesignMedia>>(url)
      .toPromise();
  }
}
